<template>
  <div class="portrait">
    <div class="delu" v-if="$store.state.ishasuser">
      <div class="nav">
        <div class="back">
          <i
            class="fa fa-angle-left"
            aria-hidden="true"
            @click="$router.go(-1)"
          ></i>
        </div>
        <div class="title">登录</div>
        <div class="yanzpassword">
          验证码登录
        </div>
      </div>
      <div class="content">
        <h2>新片场</h2>
        <div class="said">使用"新片场账号"登录场库</div>
        <van-field v-model="tel" type="tel" label="手机号" />
        <van-field v-model="password" type="password" label="密码" />
        <div class="btn">
          <button @click="acCount">登录</button>
        </div>
        <div class="login">还没有账号？注册</div>
      </div>
    </div>
    <div class="set" v-if="$store.state.comeset">
      <div class="nav">
        <div class="back">
          <i
            class="fa fa-angle-left"
            aria-hidden="true"
            @click="$router.go(-1)"
          ></i>
        </div>
        <div class="title">设置</div>
      </div>
      <div class="content">
        <div class="user">
          <img
            src="https://static.kaiyanapp.com/eyepetizer-web/assets/images/index/home_bk_3.7819585d.jpg"
            alt=""
          />
          <h4 class="username">{{ $store.state.user.tel }}</h4>
          <button class="btn" @click="show = true">退出</button>
        </div>
        <van-cell title="清除缓存图片" is-link value="0.9MB" />
        <van-cell title="播放设置" is-link />
        <van-cell title="允许非WIFI网络下载">
          <van-switch v-model="checked" size="20px" />
        </van-cell>
        <van-cell title="流量播放提醒">
          <van-switch v-model="checkeds" size="20px" />
        </van-cell>
        <van-cell title="离线缓存路径设置" is-link value="手机存储" />
        <van-cell title="给场库评分" is-link />
        <van-cell
          class="genxin"
          title="版本更新"
          is-link
          value="V5.7.7 (Build 366)"
        />
        <van-cell title="分享给好友" is-link />
        <van-cell title="关注微信公众号" is-link />
        <van-cell title="加入用户QQ群" is-link />
        <van-cell title="关于我们" is-link />
        <van-cell title="免责申明" is-link />
      </div>
      <van-popup v-model="show">
        <div class="logoff">
          <h2>提示</h2>
          <h3>确定注销?</h3>
          <div class="btn">
            <span @click="show = false">取消</span>
            <span @click="logOff">确定</span>
          </div>
        </div>
      </van-popup>
    </div>
  </div>
</template>

<script>
export default {
  name: "Portrait",
  data() {
    return {
      tel: "",
      text: "",
      digit: "",
      number: "",
      password: "",
      checked: true,
      checkeds: true,
      show: false
    };
  },
  methods: {
    //登录存储账号
    acCount() {
      let pat = /^\d{11}$/gim;
      if (pat.test(this.tel)) {
        this.$router.go(-1);
        let user = JSON.parse(window.localStorage.getItem("user"));
        //第一次存储账号/创建账号
        if (!user && this.tel && this.password) {
          window.localStorage.setItem(
            "user",
            JSON.stringify({
              tel: this.tel,
              password: this.password,
              data: { like: [], playhistory: [] }
            })
          );
        }
      }
    },
    //设置的功能
    //确定退出登录
    logOff() {
      window.localStorage.removeItem("user");
      this.$store.commit("submitIshasuser", true);
      this.$router.go(-1);
      this.$store.commit("submitSet", false);
    },
    //遮罩
    showPopup() {
      this.show = true;
    }
  }
};
</script>

<style lang="less">
.portrait {
  height: 100vh;
  .delu {
    .nav {
      height: 10vh;
      display: flex;
      color: white;
      background: black;
      align-items: center;
      .title {
        width: 50%;
        text-align: center;
      }
      .back {
        flex: 1;
        i {
          font-size: 1.5em;
          padding-left: 15px;
        }
      }
      .yanzpassword {
        flex: 1;
      }
    }
    .content {
      padding: 40px 30px;
      h2 {
        text-align: center;
        color: red;
        font-size: 2em;
        font-weight: 600;
        font-family: "楷体";
      }
      .said {
        text-align: center;
        font-size: 12px;
        line-height: 2em;
      }
      .van-cell {
        border-bottom: 1px solid rgba(216, 211, 211, 0.5);
      }
      .btn {
        margin-top: 30px;
        text-align: center;
        button {
          width: 50%;
          padding: 5px;
          border: none;
          border-radius: 15px;
          background: rgb(81, 150, 255);
          color: white;
          font-size: 16px;
        }
      }
      .login {
        text-align: center;
        font-size: 12px;
        padding: 15px 0;
      }
    }
  }
  .set {
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    .nav {
      height: 10vh;
      display: flex;
      color: white;
      background: black;
      align-items: center;
      .title {
        width: 100%;
        text-align: center;
      }
      .back {
        flex: 1;
        i {
          font-size: 1.5em;
          padding-left: 15px;
        }
      }
    }
    .content {
      flex: 1;
      overflow-y: scroll;
      height: 100vh;
      background: rgba(228, 224, 224, 0.5);
      .user {
        align-items: center;
        margin: 20px 0;
        padding: 5px 15px;
        display: flex;
        background: white;
        img {
          width: 40px;
          height: 40px;
          border-radius: 50%;
        }
        .username {
          width: 70%;
          padding: 0px 5px;
          overflow: hidden;
          font-size: 14px;
        }
        .btn {
          width: 16%;
          background: rgb(233, 61, 61);
          color: white;
          border: none;
          font-size: 12px;
          padding: 5px 0;
          border-radius: 5px;
        }
      }
      .genxin {
        margin-bottom: 20px;
      }
      .van-cell {
        padding: 13px 16px;
      }
    }
  }
  .van-popup {
    width: 80%;
    border-radius: 5px;
    .logoff {
      width: 100%;
      h2 {
        text-align: center;
        padding: 10px 0px;
      }
      h3 {
        text-align: center;
        padding-bottom: 15px;
        font-size: 12px;
      }
      .btn {
        border-top: 1px solid rgba(228, 224, 224, 1);
        span {
          text-align: center;
          width: 50%;
          display: inline-block;
          padding: 14px 0;
          color: #1989fa;
          font-weight: 550;
          &:first-child {
            border-right: 1px solid rgba(228, 224, 224, 1);
          }
        }
      }
    }
  }
}
</style>
